.flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.flex-justify {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.flex-justify-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.flex-justify-start {
  display: flex;
  justify-content: space-between;
}

.flex-row {
  display: flex;
  flex-direction: row;
}

.flex-row-center {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.flex-wrap {
  flex-wrap: wrap;
}

@media only screen and (max-width: 700px) {
  .flex-justify-wrap {
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
  }
}
