:root {
  --spacing-xl: 50px;
  --spacing-l: 30px;
  --spacing-m: 15px;
  --spacing-s: 10px;
  --spacing-xs: 5px;
  --spacing-xxs: 3px;
  --border-radius-s: 3px;
  --border-radius-m: 5px;
}

.padding-m {
  padding: var(--spacing-m);
}

.padding-s {
  padding: var(--spacing-s);
}

.padding-xs {
  padding: var(--spacing-xs);
}

.spacing-top-xs {
  margin-top: var(--spacing-xs);
}

.spacing-top-m {
  margin-top: var(--spacing-m);
}

