@import './colors.css';
@import './font.css';

:root {
  --margin-inputs: 4px 4px 4px 0;
  --border-radius: 3px;
}

body {
  padding: 0;
  margin: 0;
  color: var(--color-txt-main);
  font-family: var(--font-family), Helvetica, Arial, sans-serif;
  font-size: var(--font-size-m);
  line-height: 150%;
  overflow-y: hidden;
}

a,
a:visited {
  text-decoration: none;
  color: var(--color-bkg-button);
}

a:hover {
  color: var(--color-bkg-button-hover);
}

button {
  font-size: var(--font-size-m);
  padding: 7px;
  color: var(--color-txt-button);
  background-color: var(--color-bkg-button);
  margin: var(--margin-inputs);
  border: none;
  border-radius: var(--border-radius);
  cursor: pointer;
  outline: none;
}

button:hover {
  background-color: var(--color-bkg-button-hover);
}

button:disabled {
  background-color: var(--color-bkg-button-disabled);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}
