@import './spacing.css';
@import './colors.css';

.panel {
  display: flex;
  flex-direction: column;
  align-content: stretch;
}

.panel-header {
  background-color: #c6ced6;
  border-radius: 5px 5px 0 0;
  padding: var(--spacing-s);
}

.panel-body {
  border-width: 1px 1px 1px;
  border-style: solid;
  border-color: var(--color-gray-light);
  border-radius: 5px;
  flex-grow: 5;
}

/* don't have top border if there is a header */
.panel-header + .panel-body {
  border-width: 0 1px 1px 1px;
  border-radius: 0 0 5px 5px;
}

.panel-item {
  border-width: 0 0 1px 0;
  border-style: solid;
  border-color: var(--color-gray-light);
  padding: var(--spacing-s);
  cursor: pointer;
}

.panel-item:hover {
  background-color: #ecedf3;
}

.panel-item:last-child {
  border-style: none;
}

.panel-loading {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 30vh;
  justify-content: center;
  align-items: center;
}

.panel-message-body {
  height: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-width: 0 1px 1px 1px;
  border-color: var(--color-gray-light);
  border-style: solid;
}
