input {
  box-shadow: none;
  border: 1px solid #c6ced6;
  border-radius: 3px;
  padding: var(--spacing-xs);
  font-size: inherit;
  margin: var(--margin-inputs);
  outline: none;
}

select {
  padding: var(--spacing-xs);
  font-size: inherit;
  margin: var(--margin-inputs);
  outline: none;
  border: 1px solid #aaa;
  border-radius: 3px;
}
