#content-header {
  position: relative;
  display: flex;
  justify-content: space-between;
  z-index: 50;
  color: white;
  background-color: var(--color-options-bar);
}

@media only screen and (max-width: 700px) {
  #content-header {
    flex-direction: column;
    justify-content: flex-start;
  }
}
