:root {
  --font-family: 'Roboto';
  --font-size-xl: 20px;
  --font-size-l: 17px;
  --font-size-m: 14px;
  --font-size-s: 12px;
}

.font-size-xl {
  font-size: var(--font-size-xl);
}

.font-size-l {
  font-size: var(--font-size-l);
}

.font-size-m {
  font-size: var(--font-size-m);
}

.font-size-s {
  font-size: var(--font-size-s);
}

.font-bold {
  font-weight: bold;
}

.inactive {
  text-decoration: line-through;
}
